.possibility {
  position: relative;
  @include adaptiv-value("padding-top", 164, 140, 1);
  @include adaptiv-value("padding-bottom", 335, 130, 1);
  //@media (min-width: em(992)) and (max-width: em(1640)) {
  //  padding-top: rem(98);
  //  padding-bottom: rem(200);
  //}
  &-wrap {
    position: relative;
    &:before {
      content: '';
      top: 0;
      left: 0;
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: #29A6DD;
      mix-blend-mode: multiply;
    }
  }
  &__desc {
    width: 100%;
    max-width: 80%;
    //max-width: rem(1500);
    margin: 0 auto;
    background-image: url("./../img/bg.jpg");
    background-size: contain;
    //padding: rem(128) rem(177) rem(70) rem(175);
    @include adaptiv-value("padding-top", 128, 25, 1);
    @include adaptiv-value("padding-right", 177, 25, 1);
    @include adaptiv-value("padding-bottom", 70, 25, 1);
    @include adaptiv-value("padding-left", 175, 25, 1);
    @media (min-width: em(768)) {
      text-align: justify;
    }
    @media (min-width: em(992)) and (max-width: em(1640)) {
      padding: rem(45) rem(60) rem(33);
    }
    p {
      font-weight: 700;
    }
  }
  &__img-bg {
    &-1 {
      position: absolute;
      @include adaptiv-value("top", -100, -125, 1);
      @include adaptiv-value("left", 53, 21, 1);
      @include adaptiv-value("max-width", 241, 134, 1);
      @include adaptiv-value("max-height", 480, 266, 1);
      @include contain;
      //@media (min-width: em(768)) {
      //  @include adaptiv-value("top", -100, -205, 1);
      //}
      //@media (min-width: em(1120)) {
      //  @include adaptiv-value("top", -100, -285, 1);
      //}
      @media (min-width: em(992)) and (max-width: em(1640)) {
        @include adaptiv-value("max-width", 220, 168, 1);
        @include adaptiv-value("max-height", 420, 369, 1);
        top: auto;
        @include adaptiv-value("bottom", -60, -140, 1);
        @include adaptiv-value("left", 60, 0, 1);
      }
    }
    &-2 {
      position: absolute;
      @include adaptiv-value("top", 88, 16, 1);
      @include adaptiv-value("right", 112, 19, 1);
      max-width: rem(102);
      max-height: rem(102);
      @include contain;
      @media (min-width: em(992)) and (max-width: em(1640)) {
        display: none;
      }
    }
    &-3 {
      position: absolute;
      @include adaptiv-value("bottom", 79, 5, 1);
      @include adaptiv-value("left", 53, 7, 1);
      @include adaptiv-value("max-width", 222, 127, 1);
      @include adaptiv-value("max-height", 211, 120, 1);
      @include contain;
      @media (min-width: em(992)) and (max-width: em(1640)) {
        max-height: rem(140);
        max-width: rem(111);
        left: auto;
        bottom: auto;
        @include adaptiv-value("top", 88, 16, 1);
        @include adaptiv-value("right", 52, 19, 1);
      }
    }
    &-4 {
      position: absolute;
      //bottom: rem(57);
      //right: 0;
      //max-height: rem(388);
      //right: rem(-75);
      //bottom: rem(25);
      @include adaptiv-value("bottom", 25, -48, 1);
      @include adaptiv-value("right", -75, 2, 1);
      @include adaptiv-value("max-width", 691, 191, 1);
      @include adaptiv-value("max-height", 459, 221, 1);
      @include contain;
    }
  }
}