.footer {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  @include adaptiv-value("padding-right", 122, 20, 1);
  a {
    color: white;
  }
  &-wrap {
    position: relative;
    overflow: hidden;
    background-color: black;
  }
  &__logo {
    display: block;
    @include adaptiv-value("margin-left", -110, -90, 1);
    //margin-left: rem(-110);
    //margin-top: rem(-20);
    @include adaptiv-value("margin-top", -21, -4, 1);
    @include adaptiv-value("margin-bottom", -33, 0, 1);
    &-img {
      //@include adaptiv-value("max-width", 256, 217, 1);
      //@include adaptiv-value("max-height", 243, 111, 1);
      @include adaptiv-value("max-width", 356, 217, 1);
      @include adaptiv-value("max-height", 243, 111, 1);
      //max-height: rem(243);
      //max-width: rem(356);
      @include contain;
    }
  }
  &__content {
    display: flex;
    @include adaptiv-value("padding-top", 37, 17, 1);
    @include adaptiv-value("padding-bottom", 15, 39, 1);
    @media (max-width: em(580)) {
      flex-direction: column;
    }
    @media (min-width: 992px) and (max-width: em(1640)) {
      padding-top: rem(37);
    }
  }
  &__menu {
    display: flex;
    //flex-direction: column;
    //flex-wrap: wrap;
    //page-break-inside: avoid;
    //column-count:2;
    @media (max-width: em(640)) {
      padding-bottom: rem(20);
    }
    @media (min-width: em(460)) {
      margin-right: rem(20);
    }
    a {
      @include adaptiv-value("font-size", 25, 15, 1);
      color: white;
      white-space: nowrap;
      font-weight: 300;
      @media (min-width: em(992)) {
        font-size: rem(25) !important;
      }
    }
    &-col {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @include adaptiv-value("width", 284, 83, 1);
      &+.footer__menu-col {
        //padding-left: rem(15);
        margin-left: rem(25);
      }
    }
  }
  &__social {
    display: flex;
    align-items: center;
    @media (min-width: 992px) and (max-width: em(1640)) {
      justify-content: flex-end;
    }
    a {
      margin-right: rem(8);
      @media (min-width: em(768)) {
        margin-right: rem(16);
      }
      @media (min-width: 992px) and (max-width: em(1640)) {
        margin-right: rem(5);
      }
    }
    img {
      @include adaptiv-value("max-width", 34, 16, 1);
      @include adaptiv-value("max-height", 34, 16, 1);
      @include contain;
      @media (min-width: 992px) and (max-width: em(1640)) {
        max-width: rem(30);
        max-height: rem(30);
      }
    }
  }
  &__contact {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__links {
    a {
      white-space: nowrap;
      @include adaptiv-value("font-size", 25, 13, 1);
      @media (min-width: em(992)) {
        font-weight: 300;
        font-size: rem(25) !important;
      }
      @media (min-width: 992px) and (max-width: 1640px) {
        text-decoration-line: underline;
      }
    }
  }
}