.ticker {
  display: flex;
  align-items: center;
  background-color: #FF65B6;

  white-space: nowrap;
  overflow: hidden;
  &__desc {
    padding: rem(19) 0 rem(20) rem(64);
    font-weight: 800;
    font-size: rem(32);
    line-height: 1.3;
    color: #F9D0CE;


    padding-left: 100%;
    display: inline-block;
    animation: marquee 70s linear infinite;
    &:hover {
      animation-play-state: paused
    }
    span {
      font-weight: 800;
      font-size: rem(32);
      line-height: 1;
      color: white;
    }
  }
}

@keyframes marquee {
  0%   { transform: translate(0, 0); }
  100% { transform: translate(-100%, 0); }
}