.fashion {
  display: flex;
  @media (max-width: em(992)) {
    flex-direction: column;
  }
  @media (min-width: em(1320)) {
    justify-content: center;
  }
  p {
    font-weight: 300;
    @include adaptiv-value("font-size", 30, 21, 1);
    @media (min-width: 990px) and (max-width: em(1640)) {
      font-size: rem(23);
    }
  }
  &__headline {
    font-weight: 800;
    @include adaptiv-value("font-size", 91, 45, 1);
    @media (max-width: em(768)) {
      text-align: center;
    }
    @media (min-width: 992px) and (max-width: em(1640)) {
      font-size: rem(80);
    }
    @media (min-width: 768px) and (max-width: em(992)) {
      max-width: 90%;
      margin: 0 auto;
    }
    p {
      @media (max-width: em(768)) {
        max-width: 90%;
        margin: 0 auto;
      }
    }
  }
  &__img {
    position: relative;
    max-width: rem(666);
    max-height: rem(1128);
    margin-bottom: rem(-45);
    @media (min-width: em(992)) and (max-width: em(1640)) {
      max-width: rem(392);
    }
    @media (max-width: em(992)) {
      margin: auto;
    }
    &-hero {
      @include contain;
      @media (max-width: em(768)) {
        margin-bottom: rem(-50);
      }
    }
    &-bg {
      &-1 {
        position: absolute;
        //top: rem(284);
        //right: rem(90);
        top: 19%;
        right: 20%;
        //max-width: rem(285);
        //max-height: rem(185);
        max-width: 30%;
        max-height: rem(185);
        //margin-bottom: rem(-45);
        @include contain;
        @media (min-width: em(460)) {
          top: 25%;
        }
        @media (min-width: em(768)) {
          top: 25%;
          right: 20%;
        }
        @media (min-width: em(992)) and (max-width: em(1640)) {
          top: 20%;
        }
      }
      &-2 {
        position: absolute;
        //bottom: rem(200);
        //right: rem(170);
        bottom: 0;
        right: 20%;
        max-width:  47%;
        max-height: rem(250);
        //margin-bottom: rem(-45);
        @include contain;
        @media (min-width: em(460)) {
          bottom: 12%;
        }
        @media (min-width: em(768)) {
          bottom: 17.5%;
        }
        @media (min-width: em(992)) and (max-width: em(1640)) {
          bottom: 13%;
        }
      }
    }
    &-wrap {
      padding: rem(15);
      @include adaptiv-value("padding-bottom", 15, 0, 1);
      @media (max-width: em(1920)) {
        //min-width: 45%;
        margin: 0 rem(-15);
      }
      @media (max-width: em(992)) {
        background-color: #B8E6DC;
      }
    }
  }
  &__content {
    padding-top: rem(45);
    padding-left: rem(105);
    @include adaptiv-value("padding-bottom", 86, 47, 1);
    max-width: rem(833);
    @media (min-width: 992px) and (max-width: em(1640)) {
      //padding-right: 5%;
      padding-top: rem(27);
      padding-left: rem(27);
      max-width: rem(680);
    }
    @media (max-width: em(992)) {
      max-width: 90%;
      margin: 0 auto;
      padding-left: 0;
      align-items: center;
    }
    @media (max-width: em(1640)) {
      display: flex;
      flex-direction: column;
    }
    span {
      color: #9B005D;
      font-weight: 700;
    }
  }
  &__desc {
    padding: rem(49) 0 rem(70);
    @media (max-width: em(1640)) {
      padding-top: 0;
    }
    @media (min-width: 992px) and (max-width: em(1640)) {
      max-width: rem(660);
    }
  }
  .btn+.btn {
    margin-top: rem(21);
  }
}