// COLOR SCHEMS
// ==================================
$color_scheme_main: ('dark':#000000,
  'light': #ffffff,

  'prim': #BA0E6F,
  'prim_light': #DD348B,
  'prim_dark': #930253,
  'sec': #1F2746,
  'info': #838797,
  'accent':#22A9BF,

  'back': #f5f7f9,
  'back_shad': #F4F5F5,

  'border': #DCDFE1,
  'border_dark': #BBBDC7,
  'divider': #DEE6ED,
);
// ======================
// COLOR SCHEMS


:root {
  // FONTS
  // =====================
  --fnt_main: 'Agrandir', sans-serif;
  --fnt_sec: 'HighTide', sans-serif;
  // ===========================
  // FONTS


  // COLORS THEMES
  // ========================
  @include color_theme($color_scheme_main);

  // .theme_sec {
  //   $color_scheme_sec: ('prim': #FFD112,
  //       'sec': #CD2D2B,
  //     );
  //   @include color_theme($color_scheme_sec);
  // }

  // ==========================
  // COLORS THEMES



  // COLORS DEPENDENSIS
  // ====================
  // scrollBar
  --scrollBar-color: #b8e6dc;

  // --scrollBar-color: var(--grad_prim);
  --scrollBar-width: 0px;
  --scrollBar-height: auto;
  --scrollBar-radius: 0px;
  --scrollBar-gap: 0px;

  // Text selection color
  --text_selection_bg: #{clr(prim, 1)};
  --text_selection_clr: #{clr(light, 1)};

  // Select scroll color
  --select-scroll: #{clr(divider, 1)};
  // =================
  // COLORS DEPENDENSIS
}


