$width-col: 400;
.product {
  position: relative;
  padding-top: rem(150);
  &-wrap {
    position: relative;
    z-index: 1;
    //overflow: hidden;
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    //max-width: rem(1430);
    margin: 0 auto;
    //padding-bottom: rem(117);
    //padding: rem(147) 0 rem(117);
    //@include adaptiv-value("padding-top", 147, 328, 1);
    @include adaptiv-value("padding-bottom", 100, 0, 1);
    @media (max-width: em(768)) {
      flex-direction: column;
      align-items: center;
    }
    @media (min-width: em(1640)) {
      justify-content: space-between;
    }
    @media (min-width: em(992)) {
      max-width: 79%;
    }
    @media (min-width: em(1140)) {
      max-width: 86%;
    }
    @media (min-width: em(1420)) {
      max-width: 74%;
    }
  }
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: rem(336);
    padding-bottom: rem(140);
    @include adaptiv-value("padding-bottom", 140, 58, 1);
    //@media (min-width: 1120px) {
    //  @include adaptiv-value("max-width", 632, $width-col, 1);
    //}
    @media (min-width: em(768)) {
      //max-width: 50%;
      margin: 0 rem(10);
      width: 45%;
    }

    @media (min-width: 1040px) {
      max-width: rem(378);
    }
    @media (min-width: 1220px) {
      max-width: rem(515);
    }
    @media (min-width: 1640px) {
      max-width: rem(632);
    }
    &--1 {
      .product__item-img-bg {
        position: absolute;
        top: rem(-24);
        //right: rem(-152);
        //right: -8vw;
        //@include adaptiv-value("top", -24, -149, 1);
        //@include adaptiv-value("right", -152, 304, 1);
        @include adaptiv-value("max-width", 79, 79, 1);
        @include adaptiv-value("max-height", 79, 79, 1);
        @include contain;
        @media (min-width: 768px) and (max-width: 1220px) {
          @include adaptiv-value("top", -24, -140, 1);
          @include adaptiv-value("right", -152, 15, 1);
        }
        @media (min-width: 1220px) {
          @include adaptiv-value("top", -24, -140, 1);
          @include adaptiv-value("right", -122, 10, 1);
        }
        @media (max-width: 768px) {
          top: rem(-149);
          right: rem(144);
          max-height: rem(98);
          max-width: rem(98);
        }
      }
    }
    &--2 {
      .product__item-img-bg {
        &-1 {
          position: absolute;
          top: rem(413);
          //@include adaptiv-value("top", 413, 15, 1);
          left: rem(-154);
          //@include adaptiv-value("left", -154, 4, 1);
          @include adaptiv-value("max-width", 154, 79, 1);
          @include adaptiv-value("max-height", 154, 79, 1);
          @include contain;
          @media (min-width: 768px) {
            @include adaptiv-value("left", -154, -45, 1);
            @include adaptiv-value("top", 413, 773, 1);
          }
          @media (max-width: 768px) {
            top: rem(15);
            left: auto;
            right: rem(4);
          }
          @media (max-width: 1640px) {
            display: none;
          }
        }
        &-2 {
          position: absolute;
          top: rem(655);
          @include adaptiv-value("right", 65, 0, 1);
          max-height: rem(54);
          max-width: rem(54);
          @include contain;
        }
        &-3 {
          position: absolute;
          bottom: 0;
          right: 0;
          max-height: rem(106);
          max-width: rem(86);
          @include contain;
        }
      }
    }
    &--4 {
      .product__item-img-bg {
        &-1 {
          position: absolute;
          top: rem(-73);
          left: rem(-88);
          max-height: rem(81);
          max-width: rem(81);
          @include contain;
          @media (min-width: 768px) {
            @include adaptiv-value("left", -88, -45, 1);
          }
        }
        &-2 {
          position: absolute;
          top: rem(722);
          right: rem(-56);
          max-height: rem(79);
          max-width: rem(79);
          @include contain;
          @media (min-width: 768px) {
            @include adaptiv-value("right", -56, -56, 1);
            @include adaptiv-value("top", 722, 790, 1);
          }
        }
      }
    }
    &--5 {
      margin: 0 auto !important;
      ul {
        @media (min-width: em(992)) {
          margin-right: rem(-55);
        }
      }
      .product__item-img-bg {
        position: absolute;
        @include adaptiv-value("top", -230, -74, 1);
        @include adaptiv-value("right", 0, -27, 1);
        @include adaptiv-value("max-width", 512, 307, 1);
        @include adaptiv-value("max-height", 288, 150, 1);
        @include contain;
        @media (max-width: em(1640)) {
          max-width: rem(157);
          max-height: rem(147);
          right: 30%;
          top: rem(-90);
          //top: rem(-15);
        }
      }
    }
  }
  &__img {
    //max-width: rem(630);
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: rem(336);
    margin: 0 auto;
    @include cover;
    //@media (min-width: 1120px) {
    //  @include adaptiv-value("max-width", 630, $width-col, 1);
    //  @include adaptiv-value("max-height", 630, $width-col, 1);
    //}
    @media (min-width: 768px) {
      min-height: rem(336);
    }
    @media (min-width: 1040px) {
      max-height: rem(378);
      min-height: rem(378);
    }
    @media (min-width: 1220px) {
      max-height: rem(515);
      min-height: rem(515);
    }
    @media (min-width: 1640px) {
      max-height: rem(632);
      min-height: rem(632);
    }
    //&:after {
    //  content: '';
    //  position: absolute;
    //  top: 0;
    //  left: 0;
    //  width: 100%;
    //  height: 100%;
    //  max-height: rem(630);
    //  max-width: rem(630);
    //  background-color: #B4E2D8;
    //  mix-blend-mode: multiply;
    //}
    img {
      position: relative;
      z-index: 1;
      max-height: 100%;
      max-width: 100%;
      @include cover;
    }
    &--bg {
      mix-blend-mode: multiply;
      img {
        @media (min-width: 840px) {
          min-height: rem(336);
        }
        @media (min-width: 1040px) {
          min-height: rem(378);
        }
        @media (min-width: 1220px) {
          min-height: rem(515);
        }
        @media (min-width: 1640px) {
          min-height: rem(632);
        }
      }
    }
    &-bg {
      &-1 {
        position: absolute;
        bottom: rem(49);
        //left: rem(-328);
        //max-height: rem(887);
        //max-width: rem(888);
        @include adaptiv-value("left", -328, -150, 1);
        @include adaptiv-value("max-width", 887, 62, 1);
        @include adaptiv-value("max-height", 888, 62, 1);
        @include contain;
      }
      &-2 {
        position: absolute;
        bottom: rem(85);
        //left: rem(150);
        @include adaptiv-value("left", 150, -150, 1);
        @include adaptiv-value("max-width", 480, 62, 1);
        @include adaptiv-value("max-height", 480, 62, 1);
        @include contain;
        @media (min-width: 992px) and (max-width: 1640px) {
          max-width: rem(661);
          max-height: rem(358);
          left: rem(-150);
          bottom: rem(-92);
          //bottom: rem(-22);
        }
      }
      &-3 {
        position: absolute;
        bottom: rem(752);
        max-height: rem(607);
        @include adaptiv-value("right", 180, -180, 1);
        @include adaptiv-value("max-width", 362, 62, 1);
        @include contain;
        @media (min-width: 992px) and (max-width: 1640px) {
          max-width: rem(291);
          max-height: rem(485);
          bottom: rem(252);
        }
      }
    }
  }
  &__count {
    position: absolute;
    z-index: 1;
    @include adaptiv-value("left", 21, 12, 1);
    @include adaptiv-value("top", 16, 10, 1);
    @include adaptiv-value("max-width", 75, 69, 1);
    @include adaptiv-value("max-height", 125, 141, 1);
    @include contain;
  }
  &__title {
    font-weight: 700;
    @include adaptiv-value("padding-top", 30, 20, 1);
    //@include adaptiv-value("font-size", 40, 31, 1);
    text-align: center;
    color: #E55A41;
    font-size: rem(30);
    @media (min-width: 840px) {
      font-size: rem(25);
    }
    @media (min-width: 1040px) {
      font-size: rem(30);
    }
    @media (min-width: 1220px) {
      font-size: rem(35);
    }
  }
  ul {
    padding-top: rem(30);
    padding-left: rem(25);
    @media (min-width: 768px) and (max-width: 1640px) {
      padding-top: rem(10);
    }
    @media (min-width: 1640px) {
      padding-top: rem(60);
    }
  }
  li {
    font-size: rem(20);
    //@include adaptiv-value("font-size", 33, 21, 1);
    list-style-image: url("./../img/ico/li.svg");
    @media (min-width: 1220px) {
      font-size: rem(20);
    }
    @media (min-width: 1640px) {
      font-size: rem(30);
    }
  }
  b {
    font-weight: 700;
  }
}