.sitemap {
  font-weight: 500;
  //background-color: #04246E;
  @include adaptiv-value("padding-top", 250, 200, 1);
  &-wrap {
    max-width: 93%;
    margin: 0 auto;
  }
  &__desc {
    @include adaptiv-value("font-size", 18, 16, 1);
    padding-bottom: 30px;
  }
  h1 {
    @include adaptiv-value("font-size", 40, 22, 1);
    //color: #fff;
    font-weight: 800;
    text-transform: uppercase;
  }
  a {
    width: fit-content;
    @include adaptiv-value("font-size", 35, 19, 1);
     display: block;
     //text-transform: uppercase;
    color: black;
     padding-bottom: 15px;
    //transition: .1s;
      &:hover {
        //font-weight: 800;
        //font-size: rem(28)
        transform: scale(1.1);
        padding-left: rem(5);
      }
   }
}