.collection {
  position: relative;
  //padding: rem(76) 0 rem(142);
  &-wrap {
    position: relative;
    z-index: 1;
    &:before {
      content: '';
      top: 0;
      left: 0;
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: #70AA50;
      mix-blend-mode: multiply;
    }
  }
  &__content {
    position: relative;
    z-index: 1;
    //max-width: rem(1331);
    max-width: 74%;
    margin: 0 auto;
    @include adaptiv-value("padding-top", 440, 258, 1);
    @include adaptiv-value("padding-bottom", 287, 200, 1);
    font-weight: 800;
    text-align: center;
    color: #F6CDCB;
    @media (min-width: 992px) and (max-width: em(1600)) {
      padding: rem(255) 0 rem(203);
    }
    @media (min-width: 992px) {
      max-width: 68%;
    }
  }
  &__img-bg {
    &-1 {
      position: absolute;
      left: 0;
      top: 0;
      //bottom: rem(142);
      padding: rem(76) 0 rem(142);
      max-width: 95%;
      max-height: 60%;
      @include contain;
      mix-blend-mode: multiply;
      @media (min-width: em(992)) {
        max-width: 48%;
        max-height: 100%;
      }
    }
    &-2 {
      position: absolute;
      right: 0;
      bottom: rem(-70);
      padding: rem(76) 0 rem(142);
      max-width: 95%;
      max-height: 60%;
      @include contain;
      mix-blend-mode: multiply;
      @media (min-width: em(992)) {
        top: 0;
        bottom: auto;
        max-width: 48%;
        max-height: 100%;
      }
    }
    &-3 {
      position: absolute;
      @include adaptiv-value("left", 174, 58, 1);
      bottom: rem(36);
      //@include adaptiv-value("top", 76, 76, 1);
      @include adaptiv-value("max-height", 242, 99, 1);
      @include adaptiv-value("max-width", 242, 99, 1);
      @include contain;
      @media (min-width: em(992)) {
        bottom: auto;
        top: rem(76);
      }
      @media (min-width: 992px) and (max-width: em(1600)) {
        @include adaptiv-value("left", -117, -117, 1);
        top: auto;
        bottom: rem(61);
        @include adaptiv-value("max-height", 300, 300, 1);
        @include adaptiv-value("max-width", 300, 300, 1);
      }
    }
    &-4 {
      position: absolute;
      //right: rem(0);
      //top: rem(-125);
      //max-width: rem(989);
      //max-height: rem(537);
      @include adaptiv-value("right", -80, -48, 1);
      @include adaptiv-value("top", -125, -10, 1);
      @include adaptiv-value("max-height", 537, 239, 1);
      @include adaptiv-value("max-width", 989, 440, 1);
      @include contain;
      @media (min-width: 992px) and (max-width: em(1600)) {
        @include adaptiv-value("top", -165, -10, 1);
      }
    }
    &-5 {
      position: absolute;
      @include adaptiv-value("right", -50, -16, 1);
      @include adaptiv-value("bottom", -185, -29, 1);
      @include adaptiv-value("max-height", 472, 212, 1);
      @include adaptiv-value("max-width", 472, 212, 1);
      @include contain;
      @media (min-width: 992px) and (max-width: em(1600)) {
        max-width: rem(400);
        max-height: rem(400);
      }
    }
  }
}