@mixin font($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;
    //src:url("../fonts/Agrandir-Font-Family/Agrandir-FreeForPersonalUse/#{$file_name}.otf") format("opentype");
    //src:url("../fonts/Agrandir-Font-Family/Agrandir-FreeForPersonalUse/#{$file_name}.ttf") format("truetype");
    src:url("../fonts/Agrandir-Font-Family/Agrandir-FreeForPersonalUse/#{$file_name}.otf") format("opentype");
    font-weight: #{$weight};
    font-style: #{$style};
    // unicode-range: U+0410-044F; //только кирилица
  }
}

@mixin font-dop($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;
    //src:url("../fonts/Agrandir-Font-Family/Agrandir-FreeForPersonalUse/#{$file_name}.otf") format("opentype");
    //src:url("../fonts/Agrandir-Font-Family/Agrandir-FreeForPersonalUse/#{$file_name}.ttf") format("truetype");
    src:url("../fonts/HighTide/HighTide/Commercial/OTF/#{$file_name}.otf") format("opentype");
    font-weight: #{$weight};
    font-style: #{$style};
    // unicode-range: U+0410-044F; //только кирилица
  }
}
@import "./_font-list.scss";