.art {
  position: relative;
  padding: rem(51) rem(15) rem(120);
  @media (max-width: em(992)) {
    overflow: hidden;
  }
  @media (min-width: em(992)) and (max-width: em(1460)) {
    padding-bottom: rem(75);
    padding-top: 0;
  }
  &-wrap {
    position: relative;
    //background-color: rgba(233,231,227,.3);
    //background-blend-mode: multiply;
    //overflow: hidden;
    &:before {
      content: '';
      top: 0;
      left: 0;
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: #E9E7E3;
      mix-blend-mode: multiply;
    }
  }
  &__article {
    max-width: 79%;
    @media (min-width: em(992)) {
      padding-right: rem(15);
    }
    @media (min-width: em(1460)) and (max-width: em(1640)) {
      max-width: 49%;
    }
    @media (max-width: em(992)) {
      padding-bottom: rem(53);
    }
    @media (min-width: em(992)) and (max-width: em(1460)) {
      max-width: 55%;
    }
    @media (min-width: em(1460)) {
      max-width: 53%;
    }
  }
  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    //align-items: flex-end;
    //max-width: rem(1451);
    margin: 0 auto;
    @media (max-width: em(992)) {
      flex-direction: column;
      align-items: center;
    }
    @media (min-width: em(992)) and (max-width: em(1460)) {
      max-width: 85%;
    }
    @media (min-width: em(1120)) and (max-width: em(1460)) {
      padding-left: 7%;
    }
    @media (min-width: em(992)) and (max-width: em(1120)) {
      padding-left: 0;
    }
    @media (min-width: em(1460)) {
      max-width: 79%;
    }
  }
  &__hedline {
    position: relative;
    width: fit-content;
    justify-items: flex-start;
    font-family: var(--fnt_sec);
    @include adaptiv-value("font-size", 222, 110, 1);
    @media (max-width: em(992)) {
      text-align: center;
      margin: 0 auto;
    }
    @media (min-width: em(1460)) {
      max-width: 51%;
    }
    @media (min-width: em(992)) and (max-width: em(1460)) {
      font-size: rem(150);
      max-width: 85%;
    }
    @media (min-width: em(1120)) and (max-width: em(1460)) {
      padding-left: 7%;
    }
    @media (min-width: em(992)) and (max-width: em(1120)) {
      padding-left: 0;
    }
    &-wrap {
      max-width: 77%;
      //max-width: rem(1451);
      margin: 0 auto;
      //@media (min-width: 768px) and (max-width: em(1600)) {
      //  padding: 0 3%;
      //}
      @media (min-width: em(992)) and (max-width: em(1460)) {
        max-width: 85%;
      }
    }
    &-ico {
      position: absolute;
      top: rem(-54);
      right: rem(-60);
      @include adaptiv-value("max-width", 188, 114, 1);
      @include adaptiv-value("max-height", 217, 131, 1);
      @include contain;
      mix-blend-mode: multiply;
      @media (min-width: em(992)) and (max-width: em(1460)) {
        top: 0;
      }
    }
  }
  &__desc {
    //max-width: rem(745);
    //@media (min-width: em(992)) and (max-width: em(1560)) {
    //  max-width: rem(459);
    //}
    //@media (min-width: 1240px) and (max-width: em(1600)) {
    //  padding-right: rem(100);
    //}
    p {
      font-weight: 300;
      &+p {
        padding-top: rem(20);
        @media (min-width: em(1460)) {
          padding-top: rem(42);
        }
      }
    }
    span {
      font-weight: 700;
      color: #C7AA51;
    }
  }
  &__check {
    //max-width: rem(551);
    //margin: 0 rem(15);
    //height: fit-content;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-wrap {
      position: relative;
      text-align: center;
      border-radius: rem(58);
      border: rem(5) solid white;
      height: fit-content;
      width: 100%;
      max-width: rem(320);
      padding: rem(45) rem(14) rem(28) rem(14);
      @media (min-width: em(1460)) {
        //@include adaptiv-value("max-width", 551, 428, 1);
        padding: rem(33) rem(40) rem(58) rem(38);
      }
      @media (min-width: em(992)) and (max-width: em(1460)) {
        padding: rem(28) rem(13);
        margin-top: rem(35);
      }
      @media (max-width: em(992)) {
        margin-bottom: rem(70);
      }
      @media (min-width: em(1460)) {
        max-width: rem(511);
      }
    }
    &-title {
      font-weight: 700;
      font-size: rem(40);
      @media (min-width: em(992)) and (max-width: em(1460)) {
        font-size: rem(45);
      }
      @media (min-width: em(1460)) {
        font-size: rem(70);
      }
    }
    &-subtitle {
      font-weight: 300;
      font-size: rem(25);
      @media (min-width: em(1460)) {
        font-size: rem(35);
      }
    }
    &-desc {
      margin-top: rem(-10);
      font-weight: 700;
      line-height: 1.4;
      padding-top: rem(25);
      //padding-bottom: rem(32);
      font-size: rem(16);
      @media (min-width: em(992)) and (max-width: em(1460)) {
        padding-top: rem(9);
      }
      @media (min-width: em(1460)) {
        font-size: rem(29);
      }
    }
    .btn+.btn {
      margin-top: rem(30);
    }
  }

  &__img-bg {
    &-top {
      position: absolute;
      //top: rem(-220);
      //right: rem(-80);
      @include adaptiv-value("top", -191, -121, 1);
      @include adaptiv-value("right", -382, -83, 1);
      //@include adaptiv-value("top", -552, -71, 1);
      //@include adaptiv-value("right", -323, -83, 1);
      @include adaptiv-value("max-width", 330, 275, 1);
      @include adaptiv-value("max-height", 330, 275, 1);
      //right: 0;
      @include contain;
      //@media (min-width: em(1240)) {
      //top: rem(-730);
      //right: -50% !important;
      //@include adaptiv-value("top", -730, -450, 1);
      //@include adaptiv-value("right", -180, 0, 1);
      //}
      @media (min-width: em(992)) and (max-width: em(1460)) {
        top: rem(-400);
        @include adaptiv-value("right", -200, 0, 1);
      }
      @media (min-width: em(1460)) {
        @include adaptiv-value("top", -590, -590, 1);
        @include adaptiv-value("right", -200, 350, 1);
        @include adaptiv-value("max-width", 618, 275, 1);
        @include adaptiv-value("max-height", 618, 275, 1);
      }
    }
    &-bottom {
      position: absolute;
      bottom: rem(-41);
      right: 0;
      //right: calc(50% - rem(346));
      width: 100%;
      display: flex;
      justify-content: center;
      //@include adaptiv-value("height", 389, 320, 1);
      margin: 0 auto;
      z-index: 1;
      @media (min-width: em(992)) and (max-width: em(1460)) {
        right: rem(-100);
      }
      @media (min-width: em(1460)) {
        bottom: rem(-55);
        right: rem(-60);
      }
      img {
        @include adaptiv-value("max-width", 530, 320, 1);
        //max-width: rem(692);
        max-height: rem(267);
        @include contain;
        @media (min-width: em(992)) and (max-width: em(1460)) {
          max-height: rem(298);
        }
        @media (min-width: em(1460)) {
          @include adaptiv-value("max-width", 692, 320, 1);
          max-height: rem(389);
        }
      }
    }
    &-left {
      position: absolute;
      bottom: rem(73);
      left: rem(100);
      max-width: rem(49);
      max-height: rem(49);
      @include contain;
      mix-blend-mode: multiply;
      @media (max-width: em(992)) {
        display: none;
      }
      @media (min-width: em(992)) and (max-width: em(1120)) {
        left: rem(30);
        bottom: rem(33);
      }
    }
  }
}