.about-join {
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: rem(1404);
    margin: 0 auto;
    @media (max-width: em(768)) {
      max-width: rem(322);
    }
    @media (min-width: 768px) and (max-width: em(1600)) {
      max-width: 90%;
      justify-content: space-around;
    }
  }
  &-wrap {
    background-color: #F49FCE;
    mix-blend-mode: multiply;
    @include adaptiv-value("padding-top", 78, 37, 1);
    @include adaptiv-value("padding-bottom", 78, 31, 1);
  }
  &__tiile {
    position: relative;
    width: fit-content;
    margin: 0 auto;
    //padding: rem(52) rem(66) rem(40);
    @include adaptiv-value("padding-top", 30, 37, 1);
    @include adaptiv-value("padding-right", 38, 31, 1);
    @include adaptiv-value("padding-bottom", 23, 31, 1);
    @include adaptiv-value("padding-left", 38, 37, 1);
    text-align: center;
    font-weight: 800;
    @include adaptiv-value("font-size", 48, 22, 1);
    line-height: 1.3;
    color: #F3F3F2;
    &-wrap {
      @media (max-width: em(768)) {
        padding-bottom: rem(26);
      }
    }
    &:before {
      content: '';
      top: 0;
      left: 0;
      z-index: -1;
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-image: url("../img/join/2.svg");
      background-size: 100% 100%;
      mix-blend-mode: multiply;
      background-repeat: no-repeat;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    @include adaptiv-value("width", 146, 83, 1);
    @include adaptiv-value("height", 146, 83, 1);
    @media (min-width: 768px) and (max-width: em(1640)) {
      width: rem(77);
      height: rem(77);
    }
    &:before {
      content: '';
      top: 0;
      left: 0;
      z-index: -1;
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      mix-blend-mode: multiply;
      border-radius: 50%;
      background-color: #F6A917;
    }
    &:first-child {
      img {
        @include adaptiv-value("max-width", 76, 53, 1);
        @include adaptiv-value("max-height", 94, 43, 1);
        @media (min-width: 768px) and (max-width: em(1640)) {
          max-width: rem(49);
          max-height: rem(40);
        }
      }
    }
    img {
      @include contain;
    }
  }
}