.breadcrumps {
  display: flex;
  flex-wrap: wrap;

  // .breadcrumps__item
  &__item {
    flex: 0 0 auto;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.32px;
    color: clr(info);
    display: inline-block;
    padding: 4px 0;

    &:hover {
      color: clr(prim);
    }

    &:active {
      color: clr(prim_dark);
    }

    &:last-child {
      pointer-events: none;
    }

    &:not(:last-child) {
      &::after {
        content: ' /';
      }
    }
  }

  // .breadcrumps_gap
  &_gap {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
}
