.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  z-index: 1;
  overflow: hidden;
  //margin: 0 rem(-15);
  &-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 3;
    //@media (min-width: em(992)) {
    //  position: relative;
      //padding: 0;
    //}
  }
  &__logo {
    display: flex;
    //margin-top: rem(-12);
    @include adaptiv-value("margin-top", -12, 0, 1);
    @include adaptiv-value("margin-left", -115, -90, 1);
    //margin-left: 0;
    //margin-top: 0;
    &-img {
      //max-height: 132px;
      //max-width: 339px;
      @include adaptiv-value("max-height", 231, 132, 1);
      @include adaptiv-value("max-width", 323, 208, 1);
      @include contain;
      @media (min-width: em(992)) and (max-width: em(1460)) {
        max-width: rem(267);
        max-height: rem(191);
      }
    }
  }
  &__content {
    @media (max-width: 992px) {
      padding-bottom: rem(30);
    }
    @media (min-width: 992px) {
      display: flex;
      padding-top: rem(59);
    }
    @media (min-width: em(992)) and (max-width: em(1460)) {
      padding-top: rem(47);
    }
  }
  &__menu {
    @media (min-width: em(992)) {
      display: flex;
      //align-items: center;
      @include adaptiv-value("padding-right", 177, 15, 1);
    }
    @media (max-width: 992px) {
      padding-top: rem(28);
    }
    a {
      color: black;
      font-weight: 500;
    }
    &-item {
      font-weight: 300;
      font-size: rem(30);
      padding: 0 rem(27);
      white-space: nowrap;
      height: fit-content;
      @media (min-width: em(992)) and (max-width: em(1460)) {
        font-size: rem(20);
        padding: 0 rem(22);
      }
      @media (max-width: em(992)) {
        display: flex;
        height: rem(54);
      }
      &.active {
        font-weight: 800;
      }
    }
  }
  &__contact {
    display: flex;
    @media (max-width: em(992)) {
      padding-top: rem(11);
      padding-left: rem(27);
    }
    @media (min-width: em(992)) and (max-width: em(1460)) {
      padding-right: rem(88);
    }
    &-item {
      display: block;
      margin-right: rem(11);
      @media (min-width: em(1460)) {
        margin: 0 rem(14);
      }
      @media (min-width: em(992)) and (max-width: em(1460)) {
        margin: 0 rem(5);
      }
      &:last-child {
        margin-right: 0;
      }
      img {
        @include adaptiv-value("max-height", 41, 26, 1);
        @include adaptiv-value("max-width", 52, 33, 1);
        @include adaptiv-value("min-height", 41, 26, 1);
        @include adaptiv-value("min-width", 52, 33, 1);
        @include contain;
        @media (min-width: em(992)) and (max-width: em(1460)) {
          max-width: rem(36);
          min-width: rem(36);
          max-height: rem(29);
          min-height: rem(29);
        }
      }
    }
  }
}