.mission {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: rem(20) 0 rem(38);
  &__hero {
    max-width: rem(697);
    max-height: em(346);
    //max-width: rem(705);
    @include contain;
    @media (max-width: em(1600)) {
      max-width: rem(381);
      max-height: em(189);
    }
  }
  &__head {
    display: flex;
    align-items: center;
    @include adaptiv-value("margin-top", -45, -20, 1);
    @media (min-width: em(768)) {
      margin: rem(-80) rem(-190) rem(-20) 0;
    }
    @media (min-width: 992px) and (max-width: em(1600)) {
      margin-top: rem(-40);
      margin-bottom: rem(30);
    }
    &-ico {
      max-width: rem(325);
      //max-height: rem(325);
      //margin-left: rem(-97);
      max-height: rem(220);
      margin-left: rem(-30);
      @include contain;
      @media (max-width: em(768)) {
        display: none;
      }
      @media (min-width: 992px) and (max-width: em(1600)) {
        max-width: rem(125);
        margin-left: rem(30);
      }
    }
  }
  &__content {
    display: flex;
    //flex-wrap: wrap;
    //@media (min-width: em(768)) {
    //  margin: rem(-80) rem(-190) rem(-20) 0;
    //}
    @media (max-width: em(992)) {
      flex-direction: column;
      padding-top: rem(40);
    }
    @media (min-width: 992px) and (max-width: em(1460)) {
      max-width: 90%;
    }
    @media (min-width: 1460px) {
      max-width: 77%;
    }
  }
  &__col {
    max-width: rem(705);
    width: 100%;
    @media (max-width: em(992)) {
      padding-bottom: rem(60);
    }
    &:first-child {
      @media (min-width: 992px) and (max-width: em(1460)) {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      }
      p {
        @media (min-width: em(1460)) {
          max-width: rem(615);
        }
        @media (min-width: 992px) and (max-width: em(1460)) {
          max-width: rem(383);
          padding-bottom: rem(40);
        }
      }
    }
    &+.mission__col {
      @media (min-width: em(1460)) {
        padding-left: rem(45);
      }
      @media (min-width: 992px) and (max-width: em(1460)) {
        padding-left: 3%;
      }
    }
  }
  &__headline {
    font-weight: 800;
    @include adaptiv-value("font-size", 108, 50, 1);
    line-height: 1.2;
    white-space: nowrap;
  }
  &__desc {
    color: #1B2813;
    p {
      font-weight: 300;
      &+p {
        @include adaptiv-value("padding-top", 36, 10, 1);
      }
    }
    b {
      font-weight: 700;
    }
    span {
      color: #E35A41;
    }
  }
  &__img {
    max-width: rem(553);
    max-height: rem(384);
    @include contain;
    @media (min-width: 768px) and (max-width: em(1460)) {
      max-width: rem(358);
      max-height: rem(212);
    }
    @media (max-width: 992px) {
      margin: 0 auto;
    }
  }
}