.roadmap {
  position: relative;
  @include adaptiv-value("padding-top", 301, 328, 1);
  @include adaptiv-value("padding-bottom", 0, 218, 1);
  @media (min-width: 768px) and (max-width: 1640px) {
    padding-top: rem(134);
    padding-bottom: rem(5);
  }
  &__healine {
    font-weight: 800;
    @include adaptiv-value("font-size", 133, 39, 1);
    line-height: 1.3;
    text-align: center;
    white-space: nowrap;
    &:first-child {
      color: #E25A41;
      @include adaptiv-value("margin-bottom", -64, -17, 1);
    }
    &:nth-child(2) {
      color: #29A7DE;
      @include adaptiv-value("margin-bottom", -53, -16, 1);
      mix-blend-mode: multiply;
    }
    &:nth-child(3) {
      -webkit-text-stroke: rem(4) #FF65B2;
      -webkit-text-fill-color: white;
      mix-blend-mode: multiply;
      @include adaptiv-value("font-size", 132.5, 38.5, 1);
    }
  }
  &__img-bg {
    &-1 {
      position: absolute;
      bottom: rem(130);
      @include adaptiv-value("left", 220, 40, 1);
      @include adaptiv-value("max-width", 104, 37, 1);
      @include adaptiv-value("max-height", 104, 37, 1);
      @include contain;
      @media (min-width: em(1640)) {
        bottom: auto;
        @include adaptiv-value("top", 432, 532, 1);
      }
      @media (min-width: 768px) and (max-width: 1640px) {
        //bottom: rem(-198);
        @include adaptiv-value("left", 120, 40, 1);
        top: 50%;
        //@include adaptiv-value("bottom", -198, -208, 1);
      }
    }
    &-2 {
      position: absolute;
      @include adaptiv-value("top", 116, 115, 1);
      @include adaptiv-value("right", 98, 20, 1);
      @include adaptiv-value("max-width", 241, 144, 1);
      @include adaptiv-value("max-height", 241, 144, 1);
      @include contain;
      @media (min-width: 768px) and (max-width: 1640px) {
        max-height: rem(94);
        max-width: rem(94);
        @include adaptiv-value("right", 98, 0, 1);
      }
    }
    &-3 {
      position: absolute;
      @include adaptiv-value("top", 329, 213, 1);
      @include adaptiv-value("right", 294, 107, 1);
      @include adaptiv-value("max-width", 92, 46, 1);
      @include adaptiv-value("max-height", 92, 46, 1);
      @include contain;
      @media (min-width: 768px) and (max-width: 1640px) {
        max-height: rem(46);
        max-width: rem(46);
        @include adaptiv-value("right", 294, 4, 1);
      }
    }
    &-4 {
      position: absolute;
      @include contain;
      @include adaptiv-value("bottom", -338, 18, 1);
      @include adaptiv-value("right", -143, -60, 1);
      @include adaptiv-value("max-width", 727, 323, 1);
      @include adaptiv-value("max-height", 506, 225, 1);
      //@include adaptiv-value("max-width", 727, 273, 1);
      //@include adaptiv-value("max-height", 506, 175, 1);
      @media (min-width: em(1640)) {
        bottom: auto;
        @include adaptiv-value("top", 298, 457, 1);
      }
      @media (min-width: 768px) and (max-width: 1640px) {
        //bottom: rem(-198);
        //top: rem(0);
        @include adaptiv-value("bottom", -198, -208, 1);
      }
    }
  }
}