.btn {
  position: relative;
  display: flex;
  width: 100%;
  max-width: rem(433);
  height: rem(59);
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  border-radius: rem(52);
  line-height: 1.2;
  font-weight: 300;
  @include adaptiv-value("font-size", 36, 18, 1);
  @media (min-width: em(1640)) {
    height: rem(81);
  }
  &--white {
    width: auto;
    height: rem(59);
    padding: 0 rem(30);
    background-color: white;
    border: 3px solid black;
    font-weight: 700;
    color: black;
    @media (min-width: em(1460)) {
      height: rem(81);
      padding: 0 rem(55);
    }
  }
  &--blue {
    padding: 0 rem(30);
    background-color: #668BA0;
    font-size: rem(25) !important;
    line-height: 1.2;
    color: white;
    @media (min-width: em(1640)) {
      font-size: rem(36) !important;
    }
  }
  &--rouse {
    background-color: #EF9FC5;
    color: black;
    @media (max-width: em(560)) {
      max-width: 90%;
      margin-left: rem(-20);
    }
  }
  &--yellow {
    background-color: #FFDA51;
    color: black;
    @media (max-width: em(560)) {
      max-width: 90%;
      margin-left: rem(-20);
    }
  }
  &--black {
    height: rem(51) !important;
    width: rem(162);
    max-width: none;
    padding: rem(6) rem(30) 0;
    @include adaptiv-value("font-size", 38, 33, 1);
    color: white;
    line-height: normal;
    background: black;
    border-radius: rem(48);
    @media (min-width: 992px) and (max-width: em(1640)) {
      height: rem(40) !important;
      padding: rem(4) rem(35) 0;
      font-size: rem(30);
      width: auto;
    }
  }
  &__mark {
    position: absolute;
    top: 15%;
    //@include adaptiv-value("top", 0, 10, 1);
    right: rem(-29);
    max-width: rem(43);
    max-height: rem(43);
    min-height: rem(43);
    min-width: rem(43);
    font-size: rem(10);
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: contain;
    white-space: initial;
    font-weight: 300;
    text-align: left;
    padding: rem(5) rem(5) rem(9);
    background-repeat: no-repeat;
    @media (min-width: em(992)) {
      //margin-right: rem(15);
      padding: rem(10) rem(15) rem(22) rem(15);
    }
    @media (min-width: 992px) {
      max-width: rem(67);
      max-height: rem(67);
      min-height: rem(67);
      min-width: rem(67);
      font-size: rem(15);
      right: rem(-56);
    }
    @media (min-width: 1640px) {
      max-width: rem(92);
      max-height: rem(92);
      min-height: rem(92);
      min-width: rem(92);
      font-size: rem(21);
      right: rem(-72);
    }
    &--yellow {
      color: black;
      background-image: url("./../img/mark/yellow.webp");
    }
    &--green {
      background-image: url("./../img/mark/green.webp");
    }
  }
  &--disabled {
    pointer-events: none;
  }
}