.history {
  position: relative;
  z-index: 1;
  &-wrap {
    position: relative;
    z-index: 2;
  }
  &__content {
    padding: rem(542) 0 rem(272);
    @include adaptiv-value("margin-left", 245, 0, 1);
    font-weight: 700;
    @media (max-width: em(992)) {
      padding-left: 0;
      max-width: 78%;
    }
    @media (max-width: em(768)) {
      margin: 0 auto;
    }
    @media (min-width: em(768)) {
      max-width: 50%;
    }
    @media (min-width: em(1920)) {
      max-width: rem(995);
    }
    @media (max-width: em(992)) {
      padding-top: rem(338);
      padding-bottom: rem(320);
    }
    @media (min-width: em(992)) and (max-width: em(1640)) {
      max-width: 56%;
    }
    @media (min-width: em(1460)) and (max-width: em(1640)) {
      max-width: 59%;
    }
    @media (min-width: em(992)) and (max-width: em(1640)) {
      padding-top: rem(357);
      @include adaptiv-value("padding-bottom", 296, 126, 1);
      padding-left: 2%;
    }
    span {
      color: #D94A37;
      font-weight: 700;
    }
  }
  &__img-bg {
    //position: absolute;
    //left: 0;
    //top: 0;
    //width: 100%;
    //height: 100%;
    //@include contain;
    &-1 {
      position: absolute;
      top: rem(23);
      left: calc(50vw - rem(65));
      @include adaptiv-value("max-width", 191, 86, 1);
      @include adaptiv-value("max-height", 191, 103, 1);
      @include contain;
      @media (min-width: em(992)) {
        left: auto;
        @include adaptiv-value("top", 53, 23, 1);
        @include adaptiv-value("left", 78, 23, 1);
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        top: rem(188);
      }
    }
    &-2 {
      position: absolute;
      top: rem(43);
      //left: rem(-14);
      @include adaptiv-value("left", -14, -40, 1);
      @include adaptiv-value("max-width", 436, 221, 1);
      @include adaptiv-value("max-height", 436, 221, 1);
      @include contain;
      @media (min-width: em(560)) {
        left: rem(-14);
      }
      @media (min-width: em(992)) {
        @include adaptiv-value("top", 26, 23, 1);
        @include adaptiv-value("left", 213, 70, 1);
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        @include adaptiv-value("max-width", 336, 221, 1);
        @include adaptiv-value("max-height", 336, 221, 1);
      }
    }
    &-3 {
      position: absolute;
      top: rem(23);
      //right: rem(9);
      @include adaptiv-value("right", 9, -30, 1);
      @include adaptiv-value("max-width", 437, 241, 1);
      @include adaptiv-value("max-height", 437, 241, 1);
      @include contain;
      @media (min-width: em(560)) {
        right: rem(9);
      }
      @media (min-width: em(992)) {
        right: auto;
        @include adaptiv-value("top", -24, 23, 1);
        @include adaptiv-value("left", 689, 178, 1);
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        @include adaptiv-value("max-width", 337, 241, 1);
        @include adaptiv-value("max-height", 337, 241, 1);
      }
    }
    &-4 {
      position: absolute;
      //left: rem(996);
      @include adaptiv-value("left", 996, 200, 1);
      //left: calc(50vw - rem(327));
      top: rem(323);
      max-width: rem(191);
      max-height: rem(191);
      @include contain;
      @media (max-width: em(992)) {
        display: none;
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        display: none;
      }
    }
    &-5 {
      position: absolute;
      bottom: rem(-65);
      left: rem(-90);
      @include adaptiv-value("max-width", 803, 356, 1);
      @include adaptiv-value("max-height", 451, 200, 1);
      @include contain;
      @media (min-width: em(992)) {
        left: auto;
        bottom: auto;
        top: rem(26);
        @include adaptiv-value("right", 170, -170, 1);
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        top: rem(74);
        @include adaptiv-value("right", 230, -170, 1);
        @include adaptiv-value("max-width", 653, 356, 1);
        @include adaptiv-value("max-height", 301, 200, 1);
      }
    }
    &-6 {
      position: absolute;
      //left: rem(1462);
      //top: rem(357);
      bottom: rem(195);
      //left: rem(47);
      @include adaptiv-value("left", 47, 15, 1);
      @include adaptiv-value("max-width", 227, 93, 1);
      @include adaptiv-value("max-height", 148, 61, 1);
      @include contain;
      @media (min-width: em(560)) {
        left: rem(47);
        bottom: rem(165);
      }
      @media (min-width: em(992)) {
        bottom: auto;
        top: 33%;
        @include adaptiv-value("left", 1462, 362, 1);
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        top: 46%;
        @include adaptiv-value("left", 1582, 32, 1);
        @include adaptiv-value("max-width", 137, 93, 1);
        @include adaptiv-value("max-height", 158, 61, 1);
      }
    }
    &-7 {
      position: absolute;
      @include adaptiv-value("right", 110, -30, 1);
      top: rem(100);
      max-width: rem(97);
      max-height: rem(97);
      @include contain;
      @media (max-width: em(992)) {
        display: none;
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        top: rem(183);
        right: rem(63);
        //@include adaptiv-value("right", 110, -30, 1);
        max-width: rem(77);
        max-height: rem(77);
      }
    }
    &-8 {
      position: absolute;
      bottom: rem(45);
      //right: rem(47);
      @include adaptiv-value("right", 47, 10, 1);
      @include adaptiv-value("max-width", 372, 178, 1);
      @include adaptiv-value("max-height", 318, 152, 1);
      @include contain;
      @media (min-width: em(560)) {
        right: rem(47) !important;
      }
      @media (min-width: em(992)) {
        right: rem(128) !important;
        bottom: rem(221);
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        @include adaptiv-value("max-width", 232, 178, 1);
        @include adaptiv-value("max-height", 178, 152, 1);
        @include adaptiv-value("right", 90, 90, 1);
        bottom: rem(100);
      }
    }
    &-9 {
      position: absolute;
      bottom: rem(167);
      left: calc(50vw - rem(57));
      @include adaptiv-value("max-width", 99, 54, 1);
      @include adaptiv-value("max-height", 99, 54, 1);
      @include contain;
      //@media (min-width: em(560)) {
      //  //left: calc(50vw - rem(27));
      //}
      @media (min-width: em(1640)) {
        left: auto;
        right: rem(294);
        bottom: rem(73);
      }
      @media (min-width: em(992)) and (max-width: em(1640)) {
        left: auto;
        //right: rem(329);
        bottom: rem(62);
        @include adaptiv-value("right", 450, 329, 1);
        //@include adaptiv-value("bottom", 329, 329, 1);
      }
    }
  }
}