.wrap {
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
}

.pc-item {
  @media (max-width: 768px) {
    display: none !important;
  }
}

.pc-item-md {
  @media (max-width: em(992)) {
    display: none !important;
  }
}

.mob-item {
  @media (min-width: 768px) {
    display: none !important;
  }
}

.no_scroll{
  overflow: hidden;
}


.hr {
  height: 1px;
  background-color: clr(border_dark);
  width: 100%;
  max-width: 100%;
  display: block;
}

.divider {
  width: 1px;
  flex: 0 0 1px;
  height: 100%;
  background-color: #D9D9DE;
}

.border {
  border: 1px solid #D9D9DE;
}

html{
  height: 100%;
  overflow-x: hidden;
}
body {
  display: flex;
  flex-direction: column;

  @include adaptiv-value("font-size", 30, 19, 1);
  line-height: 1.5;
  color: black;
  overflow-x: hidden;
  @media (min-width: em(992)) and (max-width: em(1460)) {
    font-size: rem(23);
  }
}

p+p {
  padding-top: rem(35);
  @media (min-width: 768px) and (max-width: em(1640)) {
    padding-top: rem(10);
  }
}

header {
  flex: 0 0 auto;
}

.main {
  flex: 1 0 auto;
  background-image: url("./../img/bg.webp");
  background-size: contain;
}

footer {
  flex: 0 0 auto;
}

/*
    Не бачить шрифтів
    фонові зображення
*/

.back {
  background-color: clr(back);

  // .back_prim
  &_prim {
    background-color: clr(prim);
  }

  // .back_sec
  &_sec {
    background-color: clr(sec);
  }

  // .back_info
  &_info {
    background-color: clr(info);
  }
}


.invis {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
  border-width: 0;
  white-space: nowrap;
}

.last-item {
  display: none;

  &:first-child:last-child {
    display: block;
  }
}

// FLEXBOX
.fl_center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.jc_between {
  display: flex;
  justify-content: space-between;
}

.jc_center {
  display: flex;
  justify-content: center;
}

.jc_end {
  display: flex;
  justify-content: flex-end;
}

.jc_start {
  display: flex;
  justify-content: flex-start;
}

.ai_start {
  display: flex;
  align-items: flex-start;
}

.ai_center {
  display: flex;
  align-items: center;
}

.ai_base {
  display: flex;
  align-items: baseline;
}

