.cases {
  position: relative;
  max-width: rem(1140);
  margin: 0 auto;
  padding-top: rem(24);
  @include adaptiv-value("padding-bottom", 92, 30, 1);
  &-wrap {
    position: relative;
    &:before {
      content: '';
      top: 0;
      left: 0;
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: #F6C3D1;
      mix-blend-mode: multiply;
    }
  }
  &__headline {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 800;
    img {
      @include adaptiv-value("max-width", 665, 458, 1);
      @include adaptiv-value("max-height", 464, 301, 1);
      @include contain;
      @media (min-width: em(992)) and (max-width: em(1640)) {
        max-width: rem(367);
        max-height: rem(256);
      }
    }
    h2 {
      //position: absolute;
      text-transform: uppercase;
      &:first-child {
        @include adaptiv-value("margin-bottom", -190, -85, 1);
        //top: rem(30);
        //left: rem(63);
        @include adaptiv-value("font-size", 115, 60, 1);
        @media (min-width: 1240px) {
          margin-left: rem(-56);
        }
        @media (min-width: em(992)) and (max-width: em(1640)) {
          //@include adaptiv-value("margin-bottom", -190, -45, 1);
          margin-bottom: rem(-125);
          font-size: rem(100);
        }
      }
      &:last-child {
        //bottom: rem(-34);
        @include adaptiv-value("margin-top", -140, -55, 1);
        @include adaptiv-value("font-size", 115, 42, 1);
        @media (min-width: em(992)) and (max-width: em(1640)) {
          margin-top: rem(-75);
          font-size: rem(90);
        }
      }
    }
  }
  &__desc {
    padding: rem(10) 0 rem(69);
    margin: 0 auto;
    font-weight: 700;
    line-height: 1.2;
    color: white;
    @media (min-width: em(992)) and (max-width: em(1640)) {
      max-width: rem(780);
      padding-top: 0;
    }
    @media (max-width: em(992)) {
      max-width: 90%;
    }
  }
  &__items {
    display: flex;
    max-width: rem(830);
    margin-right: rem(-15);
    overflow: hidden;
    @include adaptiv-value("padding-bottom", 72, 114, 1);
    @media (min-width: 860px) {
      margin: 0 auto;
    }
  }
  &__img {    @include adaptiv-value("width", 220, 220, 1);
    @include adaptiv-value("margin-left", 47, 37, 1);
    @include adaptiv-value("margin-right", 47, 37, 1);
    img {
      @include adaptiv-value("max-height", 157, 157, 1);
      @include cover;
      margin: 0 auto;
      //@media (min-width: em(992)) and (max-width: em(1640)) {
      //  max-height: rem(163);
      //}
    }
  }
}