  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: var(--scrollBar-color);
  border: var(--scrollBar-gap) solid transparent;
  border-radius: var(--scrollBar-radius);
  background-clip: content-box;
  opacity: 0.6;
}
