.hero-main {
  position: relative;
  height: rem(850);
  max-height: rem(850);
  min-height: 100vh;
  overflow: hidden;
  //@media (min-width: em(992)) and (max-width: em(1640)) {
  //  height: 100%;
  //  max-height: rem(850);
  //}
  @media (min-width: em(1260)) {
    height: 100%;
    max-height: rem(1400);
  }
  &__bg {
    //position: absolute;
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    max-height: 100%;
    @media (min-width: em(1260)) {
      max-height: none;
    }
    //@media (max-width: em(1640)) {
    //  position: relative;
    //}
    video {
      @media (min-width: em(1260)) {
        width: 100%;
        height: 100%;
      }
    }
    img {
      @include cover;
    }
  }
  &__content {
    position: relative;
    width: calc(100% + rem(30));
    height: 100%;
    //height: 100vh;
    //height: calc(100vh - rem(169) - rem(32));
    @include adaptiv-value("padding-top", 169, 148, 1);
    @include adaptiv-value("padding-bottom", 32, 51, 1);
    display: flex;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    @media (max-width: em(1920)) {
      margin: 0 rem(-15);
    }
    @media (min-width: em(992)) {
      padding: 0 rem(67);
    }
    @media (min-width: em(1920)) {
      padding-top: rem(169);
      padding-bottom: rem(32);
    }
  }
  &__headline {
    //display: none;
    font-weight: 800;
    @include adaptiv-value("font-size", 163, 61, 1);
    line-height: 1;
    text-align: center;
    padding-bottom: rem(31);
  }
  &__desc {
    //display: none;
    @include adaptiv-value("max-width", 513, 303, 1);
    background-image: url("./../img/hero-main/bg.webp");
    align-self: flex-end;
    color: white;
    @include adaptiv-value("padding-top", 59, 21, 1);
    @include adaptiv-value("padding-right", 35, 24, 1);
    @include adaptiv-value("padding-bottom", 34, 2, 1);
    @include adaptiv-value("padding-left", 72, 28, 1);
    //&-wrap {
    //  position: absolute;
    //  bottom: -88%;
    //  right: 0;
    //  align-self: flex-end;
    //  @include adaptiv-value("padding-bottom", 34, 2, 1);
    //  //margin-bottom: -88%;
    //  //transition: 2.5s;
    //  //&.active {
    //  //  margin-bottom: 0;
    //  //}
    //}
  }
  &__title {
    font-weight: 800;
    @include adaptiv-value("font-size", 41, 25, 1);
  }
  &__text {
    //font-weight: 300;
    font-weight: 700;
    padding-top: rem(45);
    @include adaptiv-value("font-size", 33, 21, 1);
    line-height: 1.3;
  }
}