@use "sass:math";

@mixin cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

@mixin scrollbar-y-block {
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    height: 0;
    width: 0;
  }
}

@mixin scrollbar-x-block {
  overflow: hidden;
  overflow-x: scroll;
  overflow-y: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    height: 0;
    width: 0;
  }
}

