// HOLLY TABLE
// grid-template-columns: 54px repeat(4, minmax(min-content, auto));

@mixin grid-col($val) {
  display: grid;
  grid-template-columns: repeat (auto-fit, minmax($val, 1fr));
}

// Constructor
@mixin constructor($className, $propertyName, $minValue: 0, $maxValue: 80, $step: 4, $unit: 'px !important', $negative: '') {
  $i: $minValue;

  @while $i <=$maxValue {
    #{$className + $i} {
      #{$propertyName}: #{$negative + $i + $unit};
    }

    $i: $i+$step;
  }
}

// Constructor


// NTH MEDIA
@mixin nth_more($len) {

  &:nth-last-child(n+#{$len + 1}),
  &:nth-last-child(n+#{$len + 1})~& {
    @content;
  }
}

@mixin nth_less($len) {

  &:nth-last-child(-n+#{$len - 1}):first-child,
  &:nth-last-child(-n+#{$len - 1}):first-child~& {
    @content;
  }
}

// NTH MEDIA


// PADDING COLOR
// ====================================================================

@mixin padding_color ($gap_color, $gap, $base_color: #fff) {
  @if $gap {
    padding: $gap;
  }

  background-image: linear-gradient(to bottom, $base_color, $base_color),
  linear-gradient(to bottom, $gap_color, $gap_color);
  background-clip: content-box,
  padding-box;
}

// ====================================================================
// PADDING COLOR


// ELLIPSIS
// ====================================================================

@mixin ellipsis($lines:3) {
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-clamp: $lines;
  min-width: 0;
}

// ====================================================================
// ELLIPSIS


// FLUID SIZE
// ====================================================================
@function size($max, $min, $maxw: 1920, $minw: 320) {
  $size: calc(#{$min} * 1px + #{$max - $min} * (100vw - #{$minw} * 1px) / #{$maxw - $minw});
  @return $size;
}

