// COLORS
// ====================================================================

@mixin hexToHSL($color_name, $color_value) {
  --#{$color_name}_hue: #{math.div(hue($color_value), 1deg)};
  --#{$color_name}_saturation: #{saturation($color_value)};
  --#{$color_name}_lightness: #{lightness($color_value)};
}

@mixin color_theme($colors) {

  @each $color,
  $value in $colors {
    @include hexToHSL($color, $value);
  }
}

@function clr($color, $a: 1, $h: 0, $s: 0, $l: 0) {
  $color_hue: calc(var(--#{$color}_hue) + #{$h});
  $color_saturation: calc(var(--#{$color}_saturation) + #{$s * 1%});
  $color_lightness: calc(var(--#{$color}_lightness) + #{$l * 1%});
  @return unquote("hsla(#{$color_hue}, #{$color_saturation}, #{$color_lightness}, #{$a})");
}

