// BOX
@mixin box_left($width) {
  padding-left: calc(50% - #{$width} / 2);
}

@mixin box_right($width) {
  padding-right: calc(50% - #{$width} / 2);
}

// BOX


// FLEX COLUMN
// ====================================================================
@mixin flex($base:1, $cols:1) {
  flex: var(--fl, 0 0 auto);
  width: calc(100% / #{$base} * #{$cols});
  min-width: 0;
}

// ====================================================================
// FLEX COLUMN


// ABSOLUTE CENTER
// ====================================================================
@mixin abs_center($top:50%, $left:50%, $tr_top:-50%, $tr_left: -50%) {
  position: absolute;
  top: $top;
  left: $left;
  transform: translate($tr_top, $tr_left);
}

// ====================================================================
// ABSOLUTE CENTER


// ASPECT RATIO
// ====================================================================
@mixin ratio($X, $Y, $width: null, $fit:cover) {
  position: relative;

  @if $width {
    width: $width;
  }

  &:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: calc(#{$Y} / #{$X} * 100%);
  }

  .ratio__box {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    img,
    iframe {
      width: 100%;
      height: 100%;
      object-fit: $fit;
    }
  }
}

// ====================================================================
// ASPECT RATIO


// ASPECT RATIO STREATCH
// ====================================================================
// Изображение вставлять через ibg
@mixin ratio_str($X, $Y) {
  &::before {
    content: "";
    float: left;
    height: 0;
    margin-left: -1px;
    width: 1px;
    padding-top: calc(#{$Y} / #{$X} * 100%);
  }

  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

// ====================================================================
// ASPECT RATIO STREATCH


// TRIANGLE
// ====================================================================
@mixin triangle($direction: 'top', $color:#000, $height: 5px, $width:10px) {
  // Base styles for all triangles
  border-style: solid;
  height: 0;
  width: 0;

  // Cardinal Directions - can't be scalene this way
  @if($direction=='top') {
    border-color: transparent transparent $color transparent;
    border-width: 0 ($width/2) $height ($width/2);
  }

  @if ($direction=='bottom') {
    border-color: $color transparent transparent transparent;
    border-width: $height ($width/2) 0 ($width/2);
  }

  @if ($direction=='right') {
    border-color: transparent transparent transparent $color;
    border-width: ($height/2) 0 ($height/2) $width;
  }

  @if ($direction=='left') {
    border-color: transparent $color transparent transparent;
    border-width: ($height/2) $width ($height/2) 0;
  }

  // Ordinal Directions - can be scalene this way!
  @if ($direction=='top-left') {
    border-color: $color transparent transparent transparent;
    border-width: $height $width 0 0;
  }

  @if ($direction=='top-right') {
    border-color: transparent $color transparent transparent;
    border-width: 0 $width $height 0;
  }

  @if ($direction=='bottom-left') {
    border-color: transparent transparent transparent $color;
    border-width: $height 0 0 $width;
  }

  @if ($direction=='bottom-right') {
    border-color: transparent transparent $color transparent;
    border-width: 0 0 $height $width;
  }
}

