.vision {
  position: relative;
  margin: 0 auto;
  max-width: 77%;
  @include adaptiv-value("padding-top", 70, 73, 1);
  @include adaptiv-value("padding-bottom", 70, 89, 1);
  @media (min-width: 768px) {
    max-width: 69%;
  }
  @media (min-width: 992px) and (max-width: em(1600)) {
    padding-top: rem(70);
    padding-bottom: rem(43);
  }

  &-wrap {
    position: relative;
    &:before {
      content: '';
      top: 0;
      left: 0;
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: #E1C729;
      mix-blend-mode: multiply;
    }
  }
  &-inner {
    position: relative;
  }
  &__content {
    font-weight: 700;
    text-align: center;
    color: white;
    @include adaptiv-value("line-height", 52, 42, 1);

    span {
      color: #E35A41;
      font-weight: 700;
    }
  }
  &__img-bg {
    position: absolute;
    @include contain;
    @include adaptiv-value("right", 88, 0, 1);
    @include adaptiv-value("bottom", 0, -5, 1);
    @include adaptiv-value("max-width", 181, 117, 1);
    @include adaptiv-value("max-height", 181, 117, 1);
    @media (min-width: 992px) and (max-width: em(1600)) {
      @include adaptiv-value("max-width", 141, 117, 1);
      @include adaptiv-value("max-height", 141, 117, 1);
      left: rem(33);
      top: 30%;
      right: auto;
      bottom: auto;
    }
  }
}