.collab {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include adaptiv-value("padding-top", 106, 53, 1);
  @include adaptiv-value("padding-bottom", 201, 55, 1);
  width: 100%;
  max-width: rem(1190);
  margin: 0 auto;
  @media (min-width: 992px) and (max-width: em(1640)) {
    padding-bottom: rem(91);
  }
  &__content {
    display: flex;
    justify-content: space-between;
    @include adaptiv-value("padding-top", 81, 28, 1);
    @media (max-width: em(992)) {
      flex-direction: column;
      align-items: center;
    }
    @media (min-width: 768px) and (max-width: em(1640)) {
      justify-content: center;
      padding-top: rem(81);
    }
  }
  &__headline {
    font-weight: 600;
    text-align: center;
    @include adaptiv-value("font-size", 55, 33, 1);
    b {
      font-weight: 800;
    }
  }
  &__links {
    display: flex;
    @media (max-width: em(992)) {
      order: 1;
    }
    @media (min-width: 992px) and (max-width: em(1640)) {
      padding-top: rem(20);
    }
    a {
      height: fit-content;
    }
    img {
      @include adaptiv-value("max-width", 49, 33, 1);
      @include adaptiv-value("max-height", 49, 33, 1);
      margin: 0 rem(12);
      @include contain;
      @media (min-width: 768px) and (max-width: em(1640)) {
        max-width: rem(33);
        max-height: rem(33);
      }
    }
  }
  &__form {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @media (max-width: em(992)) {
      flex-direction: column;
      align-items: center;
      padding-bottom: rem(40);
    }
    @media (min-width: em(1640)) {
      margin-left: rem(110);
    }
    @media (min-width: 768px) and (max-width: em(1640)) {
      margin-left: rem(35);
      justify-content: flex-start;
    }
    &-content {
      width: 100%;
      max-width: rem(552);
      @media (min-width: em(992)) {
        margin-right: rem(97);
      }
      @media (max-width: em(992)) {
        padding-bottom: rem(29);
      }
      @media (min-width: 768px) and (max-width: em(1640)) {
        max-width: rem(456);
        margin-right: rem(35);
      }
      textarea,
      input {
        width: 100%;
        border-radius: rem(48);
        border: 4px solid black;
        font-weight: 300;
        @include adaptiv-value("padding-left", 35, 26, 1);
        @include adaptiv-value("padding-right", 35, 26, 1);
        @include adaptiv-value("font-size", 36, 25, 1);
        color: #6F6F6F;
        background-color: transparent;

        &::placeholder {
          @include adaptiv-value("font-size", 36, 25, 1);
          color: #6F6F6F;
          @media (min-width: 992px) and (max-width: em(1640)) {
            font-size: rem(36);
          }
        }
      }
      input {
        height: rem(50);
        @include adaptiv-value("margin-bottom", 41, 28, 1);
        padding-top: rem(9);
        @media (min-width: em(992)) {
          height: rem(74);
        }
        @media (min-width: 992px) and (max-width: em(1640)) {
          padding-top: rem(10);
        }
      }
      textarea {
        resize: none;
        height: rem(62);
        padding-top: rem(13);
        @media (min-width: 992px) and (max-width: em(1640)) {
          padding-top: rem(23);
          height: rem(212);
          font-size: rem(36);
        }
        @media (min-width: em(1640)) {
          height: rem(110);
          padding-top: rem(33);
        }
      }
    }
    &-btn {
      @media (min-width: 992px) and (max-width: em(1640)) {
        margin-top: rem(15);
      }
    }
  }
}