@media (max-width: 992px) {
  .burger {
    &__head {
      position: relative;
      z-index: 3;
    }
    &__arr {
      img {
        max-width: rem(19);
        max-height: rem(34);
        margin: rem(16) 0 0 rem(20);
        @include contain;
      }
    }
    &__inner {
      @include scrollbar-y-block;
    }
    &__btn{
      display: flex;
      align-items: center;
      position: absolute;
      top: rem(38);
      right: rem(20);
      width: 49px;
      height: 28px;
      cursor: pointer;
      //z-index: 2;
      & > span,
      & > span::before,
      & > span::after {
        display: block;
        position: absolute;
        width: 100%;
        height: 4px;
        background-color: #467040;
        transition-duration: .25s;
        border-radius: rem(20);
      }
      & > span {
        content: '';
        top: 4px;
        &::before {
          content: '';
          width: 100%;
          left: 0;
          top: 11px;
        }
        &::after {
          content: '';
          width: 100%;
          left: 0;
          top: 23px;
        }
      }
    }

    &__content{
      display: block;
      position: fixed;
      z-index: 1;
      visibility: hidden;
      top: 0;
      right: -100%;
      width: 288px;
      height: 100%;
      transition-duration: .25s;
      background-color: white;
    }

    &__toggle{

      &:checked {
        //& ~ .burger__btn > span{
        //  top: 13px;
        //  left: 0;
        //  width: 100%;
        //  transform: rotate(45deg);
        //  &::before{
        //    top: 0;
        //    left: 0;
        //    width: 100%;
        //    transform: rotate(0);
        //  }
        //  &::after{
        //    top: 0;
        //    left: 0;
        //    width: 100%;
        //    transform: rotate(90deg);
        //  }
        //}
        & ~ .burger__content{
          visibility: visible;
          right: 0;
        }
      }
    }
  }
}

.burger {
  &__toggle {
    position: absolute;
    opacity: 0;
    z-index: -2;
  }
  &__arr {
    @media (min-width: 992px) {
      display: none;
    }
  }
}