.team {
  max-width: rem(1538);
  @include adaptiv-value("padding-top", 77, 74, 1);
  //@include adaptiv-value("padding-bottom", 127, 0, 1);
  margin: 0 auto;
  @media (max-width: em(768)) {
    padding-bottom: rem(63);
  }
  &__headline {
    font-weight: 800;
    line-height: 1.2;
    text-align: center;
    @include adaptiv-value("font-size", 210, 61, 1);
    @media (max-width: em(768)) {
      color: #E35A41;
      padding-bottom: rem(26);
    }
    @media (min-width: 768px) and (max-width: em(1600)) {
      @include adaptiv-value("font-size", 180, 61, 1);
    }
  }
  &__content {
    @media (min-width: 768px) and (max-width: em(1600)) {
      max-width: 90%;
      margin: 0 auto;
    }
    @media (max-width: em(768)) {
      //max-width: 90%;
      //margin: auto;
      margin: 0 rem(-15);
    }
    @media (min-width: em(768)) {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
    }
    //@media (min-width: 768px) and (max-width: em(1600)) {
    //  max-width: 90%;
    //  margin: 0 auto;
    //}
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0 rem(15);
    padding-bottom: rem(90);
    @media (max-width: em(768)) {
      padding-left: 2%;
      padding-right: 2%;
    }
    @media (min-width: 768px) and (max-width: em(992)) {
      max-width: 35%;
    }
    @media (min-width: 992px) and (max-width: em(1640)) {
      max-width: 29%;
    }
    @media (min-width: em(1640)) {
      max-width: rem(414);
    }
  }
  &__img {
    max-width: rem(441);
    max-height: rem(417);
    margin: 0 auto;
    @include contain;
    @media (min-width: 992px) and (max-width: em(1600)) {
      //max-width: rem(280);
      max-height: rem(265);
      max-width: 80%;
    }
  }
  &__desc {
    padding-top: rem(6);
    font-size: rem(35);
    line-height: 1.3;
    text-align: center;
    span {
      display: block;
      font-weight: 700;
    }
    @media (min-width: 768px) and (max-width: em(1600)) {
      font-size: rem(23);
    }
  }
  &__contact {
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-width: rem(33);
      max-height: rem(30);
      margin: 0 rem(5);
      @include contain
    }
  }
}