.hero-about {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-top: rem(150);
  //height: 100vh;
  @media (max-width: em(992)) {
    height: rem(945);
  }
  &__headline {
    text-align: center;
    font-weight: 800;
    @include adaptiv-value("font-size", 133, 55, 1);
    line-height: 1.3;
    color: #E35A41;
    @media (max-width: em(992)) {
      line-height: 0.8;
    }
    @media (min-width: em(992)) and (max-width: em(1640)) {
      font-size: rem(107);
    }
  }
  &__img {
    max-width: rem(1936);
    max-height: rem(1291);
    @include adaptiv-value("margin-top", 0, -100, 1);
    @include contain;
    @media (min-width: em(992)) and (max-width: em(1640)) {
      margin: rem(-95) auto rem(-30);
      max-width: rem(1088);
      max-height: rem(560);
    }
    @media (max-width: em(992)) {
      margin-left: rem(-15);
      min-width: 100vw;
    }

    &-bg {
      &-l {
        position: absolute;
        @include adaptiv-value("left", 190, 47, 1);
        top: rem(344);
        @include adaptiv-value("max-width", 104, 69, 1);
        @include adaptiv-value("max-height", 104, 69, 1);
        @include contain;
        mix-blend-mode: multiply;
      }
      &-r {
        position: absolute;
        @include adaptiv-value("right", 91, 0, 1);
        @include adaptiv-value("bottom", 53, 133, 1);
        @include adaptiv-value("max-width", 242, 144, 1);
        @include adaptiv-value("max-height", 242, 144, 1);
        @include contain;
        mix-blend-mode: multiply;
        @media (min-width: em(992)) and (max-width: em(1640)) {
          max-height: rem(106);
          max-width: rem(106);
        }
      }
    }
  }
}