.comunity {
  position: relative;
  padding: 0 rem(15);
  &-wrap {
    position: relative;
    //overflow: hidden;
  }
  &__inner {
    position: relative;
    z-index: 1;
    overflow: hidden;
    margin: 0 rem(-15);
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    //padding-bottom: rem(81);
    //@include adaptiv-value("padding-left", 210, 210, 1);
    @media (min-width: em(1640)) {
      padding-left: rem(210);
      //@include adaptiv-value("padding-left", 210, 0, 1);
    }
    @media (min-width: 992px) and (max-width: em(1640)) {
      padding-left: 15%;
    }
    p {
      font-weight: 300;
    }
  }
  &__headline {
    position: absolute;
    //top: rem(-15);
    //@include adaptiv-value("top", -15, -70, 1);
    top: rem(54);
    //@media (min-width: em(1420)) {
    //}
    //top: 0;
    left: rem(0);
    //left: 0;
    //@include adaptiv-value("max-height", 1016, 860, 1);

    @media (min-width: 992px) and (max-width: 1420px) {
      left: 12%;
    }
    //@media (min-width: 768px) and (max-width: 1420px) {
    //  left: 3%;
    //}
    img {
      max-width: rem(960);
      max-height: rem(1516);
      @include contain;
      @media (max-width: 1420px) {
        margin-left: rem(-170);
      }
      @media (max-width: 768px) {
        //margin-left: -110px;
        @include adaptiv-value("margin-left", -110, -60, 1);
      }
    }
  }
  &__desc {
    padding-bottom: rem(45);
    padding-top: 40%;
    max-width: rem(721);
    //@include adaptiv-value("padding-top", 200, 120, 1);

    @media (min-width: 1120px) and (max-width: 1640px) {
      max-width: 50%;
      padding-top: 24%;
    }
    @media (min-width: 768px) {
      padding-top: rem(245);
    }
    @media (min-width: 992px) {
      padding-top: rem(270);
    }
    @media (max-width: 992px) {
      max-width: 90%;
      margin: 0 auto;
    }
    b {
      font-weight: 700;
    }
    span {
      color: #45713A;
    }
  }
  &__img {
    @media (max-width: em(1920)) {
      max-width: 47%;
    }
    img {
      max-width: rem(785);
      height: rem(785);
      margin-bottom: rem(45);
      @include contain;
      @media (max-width: em(1120)) {
        display: none;
      }
    }
    &-bg {
      position: absolute;
      @include adaptiv-value("top", -197, -72, 1);
      @include adaptiv-value("right", 0, -50, 1);
      @include adaptiv-value("max-width", 414, 187, 1);
      @include adaptiv-value("max-height", 414, 187, 1);
      @include contain;
      @media (min-width: 768px) and (max-width: em(1640)) {
        right: rem(-100);
        //@include adaptiv-value("right", 0, -50, 1);
      }
    }
  }
}