.join {
  display: flex;
  @media (max-width: em(1120)) {
    flex-direction: column;
    align-items: center;
  }
  &-wrap {
    padding: 0;
  }
  &__btn {
    position: relative;
    width: 100%;
    //height: 100%;
    //max-height: 405px;
    @include adaptiv-value("height", 405, 181, 1);
    height: 405px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include adaptiv-value("font-size", 55, 25, 1);
    text-align: center;
    text-transform: uppercase;
    @media (min-width: em(1120)) {
      max-width: 50%;
      //height: 405px;
    }
    @media (min-width: 768px) and (max-width: em(1640)) {
      @include adaptiv-value("font-size", 55, 15, 1);
    }
    span {
      position: relative;
      z-index: 1;
      font-weight: 800;
    }
    &--left {
      background-color: #F2D82C;
      mix-blend-mode: multiply;
      span {
        color: #0078BE;
        max-width: rem(580);
      }
      .join__bg {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        //max-width: rem(716);
        max-height: rem(263);
        margin: auto;
        padding: 0 10%;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, .25));
        @media (max-width: em(1120)) {
          padding: 2% 10%;
        }
        &-ico {
          @include adaptiv-value("right", 66, 20, 1);
        }
      }
    }
    &--right {
      color: #FF8640;
      background-color: #FF65B6;
      .join__bg {
        width: 100%;
        //max-width: rem(716);
        //max-height: rem(263);
        &-ico {
          @include adaptiv-value("right", 100, 10, 1);
        }
      }
      span {
        color: #FF8640;
        padding: rem(8) rem(26);
        background-color: #9660B3;
      }
    }
  }
  &__bg {
    position: absolute;
    top: auto;
    left: auto;
    max-height: 405px;
    height: 100%;
    @include contain;
    &-ico {
      position: absolute;
      z-index: 1;
      @include adaptiv-value("top", 56, 27, 1);
      @include contain;
      @include adaptiv-value("max-width", 136, 61, 1);
      @include adaptiv-value("max-height", 104, 49, 1);
    }
  }
}