.education {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding: rem(42) rem(15) rem(130);
  //@media (max-width: em(1630)) {
  //  overflow: hidden;
  //}
  @media (min-width: em(992)) and (max-width: em(1460)) {
    padding-bottom: rem(80);
  }
  &-wrap {
    position: relative;
    z-index: 1;
    background-color: #B8E6DC;
  }
  &__headline {
    position: relative;
    @include adaptiv-value("font-size", 300, 49, 1);
    //@include adaptiv-value("font-size", 300, 72, 1);
    font-weight: 700;
    line-height: 1.3;
    color: rgba(0, 0, 0, .3);
    //border: 10px solid rgba(0, 0, 0, .3);
    text-transform: uppercase;
    //-webkit-text-stroke: 50px rgba(0, 0, 0, .3);
    //-webkit-text-fill-color: rgba(0, 0, 0, 0);
    white-space: nowrap;
    @media (min-width: em(992)) and (max-width: em(1640)) {
      @include adaptiv-value("font-size", 200, 49, 1);
      font-weight: 800;
    }
    span {
      @include adaptiv-value("font-size", 300, 49, 1);
      position: relative;
      display: inline-block;
      font-weight: 700;
      line-height: 1.3;
      color: rgba(0, 0, 0, .3);
      text-transform: uppercase;
      @media (min-width: em(992)) and (max-width: em(1640)) {
        @include adaptiv-value("font-size", 200, 49, 1);
        font-weight: 800;
      }
    }
    &-mark {
      position: absolute;
      @include adaptiv-value("top", 20, -25, 1);
      @include adaptiv-value("left", 80, 5, 1);
      max-width: rem(80);
      max-height: rem(55);
      @include contain;
      @media (min-width: em(992)) and (max-width: em(1640)) {
        @include adaptiv-value("top", -10, -40, 1);
        @include adaptiv-value("left", 55, 20, 1);
      }
    }
    &-img {
      position: absolute;
      @include adaptiv-value("top", -8, -5, 1);
      @include adaptiv-value("left", 173, -20, 1);
      max-width: rem(134);
      @include adaptiv-value("max-height", 152, 57, 1);
      @include contain;
      span {
        position: relative;
      }
    }
  }
  &__desc {
    position: relative;
    z-index: 1;
    //max-width: rem(1352);
    @include adaptiv-value("margin-top", -59, 35, 1);
    padding-bottom: rem(57);
    line-height: 1.3;
    max-width: 80%;
    @media (min-width: em(992)) {
      max-width: 70%;
    }
    @media (min-width: em(992)) and (max-width: em(1640)) {
      margin-top: 0;
    }
    b {
      font-weight: 700;
    }
    span {
      color: #FF65B6;
    }
    p {
      font-weight: 300;
      &+p {
        padding-top: rem(34);
        @media (min-width: em(992)) and (max-width: em(1460)) {
          padding-top: rem(20);
        }
      }
    }
  }
  &__img-bg {
    &-right {
      position: absolute;
      bottom: rem(214);
      @include adaptiv-value("max-width", 198, 171, 1);
      @include adaptiv-value("max-height", 198, 171, 1);
      //@include adaptiv-value("right", 67, -92, 1);
      @include adaptiv-value("right", 67, -92, 1);
      @media (min-width: em(992)) and (max-width: em(1460)) {
        max-height: rem(149);
        max-width: rem(149);
        @include adaptiv-value("right", 67, 0, 1);
        bottom: rem(100);
      }
    }
    &-left {
      position: absolute;
      //@include adaptiv-value("left", 0, -57, 1);
      @include adaptiv-value("left", 110, 0, 1);
      @include adaptiv-value("bottom", -88, -68, 1);
      @include adaptiv-value("max-width", 634, 270, 1);
      @include adaptiv-value("max-height", 356, 152, 1);
      @media (min-width: em(992)) and (max-width: em(1460)) {
        max-height: rem(206);
        max-width: rem(368);
      }
      @media (min-width: em(992)) and (max-width: em(1460)) {
        @include adaptiv-value("left", 110, 150, 1);
      }
    }
  }
  &__btn {
    @media (max-width: em(640)) {
      max-width: 80%;
    }
  }
}