        margin-top: rem(-504);
      }
    }
    &-bg {
      position: absolute;
      @include adaptiv-value("top", 836, 358, 1);
      @include adaptiv-value("right", -275, -170, 1);
      @include adaptiv-value("max-width", 1014, 417, 1);
      @include adaptiv-value("max-height", 1015, 417, 1);
      @include contain;
      mix-blend-mode: multiply;
    }
  }
  &__ico {
    display: inline-block !important;
    vertical-align: middle;
    @include adaptiv-value("max-width", 53, 28, 1);
    @include adaptiv-value("max-height", 53, 28, 1);
    @include contain;
  }

  &__head {
    @media (max-width: em(768)) {
      display: flex;
      justify-content: space-between;
    }
  }
  &__content {
    @media (max-width: em(768)) {
      display: flex;
      flex-direction: column;
    }
  }
  &__col {
    @media (max-width: em(768)) {
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
}